import React from 'react';
import { Layout } from 'antd';

// 
import Logo from '../assets/logo/angkringan247-logo.png'

// 
const { Header } = Layout;

export default function Navbars() {
    return (
        <Header className='p-24 bg-[#010101]'>
            <div>
                <nav>
                    <ul className='flex justify-center items-center text-sm tracking-widest gap-20 text-[#2bb67d]'>
                        <li className='hidden md:block'><a href='.'>HOME</a></li>
                        <li className='hidden md:block'><a href='.'>NEWS <i className="fa-solid fa-chevron-down"></i></a></li>
                        <img className="translate-x md:translate-x-1/4 h-28 relative w-auto z-10" src={Logo} alt='Angkringan247 Logo'></img>
                        <li className='hidden md:block'><a href='.'>LEADERBOARD</a></li>
                        <li className='hidden md:block'><a href='.'>LOGIN</a></li>
                    </ul>
                </nav>
            </div>
        </Header>
    )
};