import React from 'react';
import { Layout } from 'antd';

// 
import { useEffect, useState } from "react";

// 
const { Content } = Layout;

export default function Contents() {

    const [tes, setTes] = useState([])

    useEffect(()=> {
        fetch('https://discord.com/api/v9/invites/angkringan247?with_counts=true&with_expiration=true')
        .then(response => response.json())
        .then(data => setTes(data.approximate_member_count))
        .catch(err => console.log(err))
    }, [])
    // console.log(tes)

    return (
        <Content className="my-10 flex justify-center items-center bg-[#010101]">
            <div className='flex justify-center text-white'>
                <div className='font-montserrat my-auto text-center'>
                    <p className='text-[#94a1b1] text-md md:text-2xl tracking-widest'>DISCORD COMMUNITY</p>
                    <p className='font-stretchpro text-2xl md:text-7xl text-[#ffffff] py-3'>ANGKRINGAN247</p>
                    <p className='tracking-widest text-md text-[#94a1b1] md:text-4xl'><span className='mr-2 text-[#2bb67d]'>{tes}</span>TOTAL MEMBERS</p>
                    <div className='mt-16'>
                        <a href='https://discord.gg/angkringan247' className='px-10 py-4 bg-[#2bb67d] text-[#010101] font-bold tracking-widest text-sm md:text-xl'>LETS JOIN ON DISCORD</a>
                    </div>
                </div>
            </div>
        </Content>
    )
};